import uniRate from '../../components/uni-ui/uni-rate/uni-rate.vue'
import guessULike from '../../components/index/guessULike/guessULike.vue'
export default {
	components: {
		uniRate,
		guessULike
	},
	data() {
		return {
			wheelList: [],
			superheroList: [],
			trailerList: [],
			likeList: [],
			num: 0
		};
	},
	onLoad() {
		this.getWheel();
		this.getSuperhero();
		this.getTrailer();
		this.getLike();
	},
	onPullDownRefresh() {
		let that = this
		setTimeout(function() {
			uni.showToast({
				title: '刷新成功'
			});
			that.getLike();
			uni.stopPullDownRefresh();
		}, 3000);
	},
	methods: {
		//获取轮播图
		getWheel() {
			uni.request({
				url: `${this.$api}/index/carousel/list?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.wheelList = res.data.data;
					console.log(res, '轮播图');
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 获取热门超英
		getSuperhero() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=superhero`,
				method: 'POST',
				data: {},
				success: res => {
					this.superheroList = res.data.data;
					console.log(res, '热门超英');
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 获取热门预告
		getTrailer() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=trailer`,
				method: 'POST',
				data: {},
				success: res => {
					this.trailerList = res.data.data;
					console.log(res, '热门预告');
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 控制播放
		controlPlay(id) {
			this.trailerList.map(item => {
				if(item.id !== id) {
					let videoContext = uni.createVideoContext(item.id)
					videoContext.pause()
				}
			})
		},
		// 获取猜你喜欢
		getLike() {
			uni.request({
				url: `${this.$api}/index/guessULike?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.likeList = res.data.data;
					console.log(res, '猜你喜欢');
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
};
